/**
 * Composant Image optimisé avec WebP, lazy loading, responsive sizes
 * Wrapper autour de next/image avec optimisations par défaut
 */
'use client';

import Image from 'next/image';
import { useState } from 'react';

interface OptimizedImageProps {
  src: string;
  alt: string;
  width?: number;
  height?: number;
  fill?: boolean;
  sizes?: string;
  priority?: boolean;
  className?: string;
  objectFit?: 'contain' | 'cover' | 'fill' | 'none' | 'scale-down';
  quality?: number;
  placeholder?: 'blur' | 'empty';
  blurDataURL?: string;
  onError?: () => void;
}

const DEFAULT_BLUR_DATA_URL =
  'data:image/webp;base64,UklGRiIAAABXRUJQVlA4IBYAAAAwAQCdASoBAAEADsD+JaQAA3AAAAAA';

export function OptimizedImage({
  src,
  alt,
  width,
  height,
  fill = false,
  sizes,
  priority = false,
  className = '',
  objectFit = 'cover',
  quality = 85,
  placeholder = 'blur',
  blurDataURL = DEFAULT_BLUR_DATA_URL,
  onError,
}: OptimizedImageProps) {
  const [imgSrc, setImgSrc] = useState(src);
  const [hasError, setHasError] = useState(false);

  // Fallback si image échoue
  const handleError = () => {
    if (!hasError && imgSrc !== '/placeholder-product.jpg') {
      setHasError(true);
      setImgSrc('/placeholder-product.jpg');
      onError?.();
    }
  };

  // Sizes responsive par défaut si non fourni
  const defaultSizes = sizes || '(max-width: 640px) 100vw, (max-width: 1024px) 50vw, 33vw';

  // Props communes
  const objectFitClass = objectFit === 'cover' ? 'object-cover' : objectFit === 'contain' ? 'object-contain' : objectFit === 'fill' ? 'object-fill' : objectFit === 'none' ? 'object-none' : 'object-scale-down';
  const commonProps = {
    src: imgSrc,
    alt,
    className: `${className} ${objectFitClass}`.trim(),
    quality,
    placeholder,
    blurDataURL,
    onError: handleError,
    loading: priority ? ('eager' as const) : ('lazy' as const),
    sizes: defaultSizes,
  };

  if (fill) {
    return <Image {...commonProps} fill />;
  }

  if (!width || !height) {
    // Si pas de dimensions, utiliser aspect ratio 16:9 par défaut
    const defaultWidth = width || 400;
    const defaultHeight = height || (defaultWidth * 9) / 16;
    return <Image {...commonProps} width={defaultWidth} height={defaultHeight} />;
  }

  return <Image {...commonProps} width={width} height={height} />;
}









