/**
 * Card produit avec bouton "Ajouter au panier"
 */
'use client';

import { AddToCartButton } from '@/components/AddToCartButton';
import { OptimizedImage } from '@/components/OptimizedImage';
import Link from 'next/link';

export interface Product {
  id: string;
  title: string;
  description: string | null;
  price: number;
  category: string | null;
  stock: number;
  images: string[];
  tags: string[];
  created_at: string;
  updated_at: string;
  is_deleted: boolean;
}

interface ProductCardProps {
  product: Product;
}

export function ProductCard({ product }: ProductCardProps) {
  return (
    <div className="group hover:shadow-xl transition-all duration-300 bg-white dark:bg-gray-800 rounded-xl overflow-hidden h-full flex flex-col">
      {/* Image */}
      <Link href={`/products/${product.id}`} className="relative w-full h-80 bg-gradient-to-br from-pink-50 to-purple-50 dark:from-gray-700 dark:to-gray-800">
        <OptimizedImage
          src={product.images[0] || '/placeholder-product.jpg'}
          alt={product.title}
          fill
          className="group-hover:scale-105 transition-transform duration-500"
          sizes="(max-width: 640px) 100vw, (max-width: 1024px) 50vw, 33vw"
          objectFit="cover"
        />
        <div className="absolute top-3 right-3 flex items-center gap-2">
          {product.stock === 0 && (
            <div className="bg-red-500 text-white px-2 py-1 rounded-full text-xs font-bold">
              Rupture
            </div>
          )}
        </div>
      </Link>

      {/* Content */}
      <div className="p-6 flex flex-col flex-grow">
        <h3 className="font-semibold text-lg line-clamp-2 leading-tight mb-2 group-hover:text-pink-600 dark:group-hover:text-pink-400 dark:text-white transition-colors">
          {product.title}
        </h3>
        {product.description && (
          <p className="text-sm text-gray-600 dark:text-gray-300 line-clamp-2 mb-4 flex-grow">
            {product.description}
          </p>
        )}
        <div className="flex items-center justify-between mb-4">
          <span className="text-2xl font-bold text-emerald-600">
            {product.price.toFixed(2)}€
          </span>
          <span className="text-sm text-gray-500 dark:text-gray-400">
            Stock: {product.stock}
          </span>
        </div>
        <AddToCartButton product={product} className="w-full" />
      </div>
    </div>
  );
}
