/**
 * Composant cours en vedette pour la page d'accueil
 */
'use client';

import { CourseLevelBadge } from '@/components/CourseLevelBadge';
import { coursesData } from '@/lib/data/courses.data';
import Link from 'next/link';

export function FeaturedCourses() {
  // Utiliser directement les données sans vérification mounted
  // Les données sont statiques donc pas de problème d'hydratation
  const featuredCourses = coursesData.slice(0, 3);

  return (
    <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
      {featuredCourses.map((course) => (
        <div
          key={course.id}
          className="bg-white rounded-xl overflow-hidden shadow-md hover:shadow-xl transition-all"
        >
          <Link href={`/courses/${course.id}`} className="relative block h-48 bg-gradient-to-br from-pink-50 to-purple-50">
            <div className="w-full h-full flex items-center justify-center text-gray-400">
              <svg className="w-16 h-16" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 6.253v13m0-13C10.832 5.477 9.246 5 7.5 5S4.168 5.477 3 6.253v13C4.168 18.477 5.754 18 7.5 18s3.332.477 4.5 1.253m0-13C13.168 5.477 14.754 5 16.5 5c1.747 0 3.332.477 4.5 1.253v13C19.832 18.477 18.247 18 16.5 18c-1.746 0-3.332.477-4.5 1.253" />
              </svg>
            </div>
            {course.badge && (
              <div className="absolute top-3 right-3">
                <span
                  className={`px-3 py-1 rounded-full text-xs font-bold text-white ${
                    course.badge === 'Nouveau'
                      ? 'bg-green-500'
                      : course.badge === 'Populaire'
                      ? 'bg-pink-600'
                      : 'bg-blue-600'
                  }`}
                >
                  {course.badge}
                </span>
              </div>
            )}
            <div className="absolute top-3 left-3">
              <CourseLevelBadge level={course.level} />
            </div>
          </Link>
          <div className="p-6">
            <h3 className="font-semibold text-lg mb-2 hover:text-pink-600 transition-colors">
              <Link href={`/courses/${course.id}`}>{course.title}</Link>
            </h3>
            <p className="text-sm text-gray-600 mb-4 line-clamp-2">{course.description}</p>
            <div className="flex items-center justify-between">
              <span className="text-2xl font-bold text-emerald-600">{course.price}€</span>
              <Link
                href={`/courses/${course.id}`}
                className="text-pink-600 hover:text-pink-700 font-medium text-sm"
              >
                Voir le cours →
              </Link>
            </div>
          </div>
        </div>
      ))}
    </div>
  );
}
