/**
 * Composant "Produits récemment consultés"
 */
'use client';

import { viewHistoryStore } from '@/lib/store/viewHistoryStore';
import Image from 'next/image';
import Link from 'next/link';
import { useEffect, useState } from 'react';

interface ViewedProduct {
  id: string;
  title: string;
  price: number;
  image: string;
  viewedAt: string;
}

interface RecentlyViewedProps {
  limit?: number;
  title?: string;
  showEmpty?: boolean;
}

export function RecentlyViewed({ limit = 8, title = 'Récemment consultés', showEmpty = false }: RecentlyViewedProps) {
  const [items, setItems] = useState<ViewedProduct[]>([]);
  const [isLoading, setIsLoading] = useState(true);

  useEffect(() => {
    const loadHistory = () => {
      const recent = viewHistoryStore.getRecent(limit);
      setItems(recent);
      setIsLoading(false);
    };

    loadHistory();

    // Écouter les mises à jour cross-tab
    const handleUpdate = () => loadHistory();
    window.addEventListener('viewHistoryUpdated', handleUpdate);

    return () => {
      window.removeEventListener('viewHistoryUpdated', handleUpdate);
    };
  }, [limit]);

  if (isLoading) {
    return (
      <div className="py-8">
        <div className="animate-pulse space-y-4">
          <div className="h-6 bg-gray-200 dark:bg-gray-700 rounded w-48"></div>
          <div className="grid grid-cols-2 sm:grid-cols-4 lg:grid-cols-8 gap-4">
            {[...Array(limit)].map((_, i) => (
              <div key={i} className="aspect-square bg-gray-200 dark:bg-gray-700 rounded"></div>
            ))}
          </div>
        </div>
      </div>
    );
  }

  if (items.length === 0) {
    if (!showEmpty) return null;

    return (
      <section className="py-8">
        <h2 className="text-2xl font-bold text-gray-900 dark:text-gray-100 mb-6">{title}</h2>
        <p className="text-gray-500 dark:text-gray-400 text-center py-8">
          Aucun produit consulté récemment
        </p>
      </section>
    );
  }

  return (
    <section className="py-8">
      <div className="flex items-center justify-between mb-6">
        <h2 className="text-2xl font-bold text-gray-900 dark:text-gray-100">{title}</h2>
        {items.length > limit && (
          <Link
            href="/account?tab=history"
            className="text-sm text-pink-600 hover:text-pink-700 dark:text-pink-400 dark:hover:text-pink-300"
          >
            Voir tout →
          </Link>
        )}
      </div>

      <div className="grid grid-cols-2 sm:grid-cols-4 lg:grid-cols-8 gap-4">
        {items.map((item) => (
          <Link
            key={item.id}
            href={`/products/${item.id}`}
            className="group relative aspect-square bg-white dark:bg-gray-800 rounded-lg overflow-hidden border border-gray-200 dark:border-gray-700 hover:border-pink-500 dark:hover:border-pink-500 transition-all hover:shadow-lg"
          >
            <div className="relative w-full h-full">
              <Image
                src={item.image || '/placeholder-product.jpg'}
                alt={item.title}
                fill
                className="object-cover group-hover:scale-105 transition-transform duration-200"
                sizes="(max-width: 640px) 50vw, (max-width: 1024px) 25vw, 12.5vw"
              />
            </div>
            <div className="absolute bottom-0 left-0 right-0 bg-gradient-to-t from-black/60 to-transparent p-2">
              <p className="text-white text-xs font-medium line-clamp-1">{item.title}</p>
              <p className="text-white text-sm font-bold">{item.price.toFixed(2)}€</p>
            </div>
          </Link>
        ))}
      </div>
    </section>
  );
}









