/**
 * Bouton "Ajouter au panier"
 * Toast notification + optimistic update
 */
'use client';

import { ecommerce } from '@/lib/analytics';
import { cartStoreSimple } from '@/lib/store/cartStoreSimple';
import { useState } from 'react';

interface Product {
  id: string;
  title: string;
  price: number;
  images: string[];
  stock: number;
}

interface AddToCartButtonProps {
  product: Product;
  className?: string;
}

export function AddToCartButton({ product, className = '' }: AddToCartButtonProps) {
  const [isAdding, setIsAdding] = useState(false);
  const [showToast, setShowToast] = useState(false);

  const handleAddToCart = async () => {
    if (product.stock === 0) {
      alert('Produit en rupture de stock');
      return;
    }

    setIsAdding(true);

    // Optimistic update
    cartStoreSimple.addItem({
      id: product.id,
      title: product.title,
      price: product.price,
      image: product.images[0] || '/placeholder-product.jpg',
      stock: product.stock,
      type: 'product', // Marquer comme produit
    });

    // Analytics
    ecommerce.addToCart(product.id, product.title, product.price, 1);

    // Toast notification
    setShowToast(true);
    setTimeout(() => setShowToast(false), 3000);

    setIsAdding(false);
  };

  return (
    <>
      <button
        onClick={handleAddToCart}
        disabled={isAdding || product.stock === 0}
        className={`
          ${className}
          px-6 py-3 bg-pink-600 text-white font-semibold rounded-lg
          hover:bg-pink-700 active:scale-95 transition-all
          disabled:opacity-50 disabled:cursor-not-allowed
          focus:outline-none focus:ring-2 focus:ring-pink-500 focus:ring-offset-2
        `}
        aria-label={`Ajouter ${product.title} au panier`}
      >
        {isAdding ? (
          <span className="flex items-center gap-2">
            <svg className="animate-spin h-5 w-5" viewBox="0 0 24 24">
              <circle
                className="opacity-25"
                cx="12"
                cy="12"
                r="10"
                stroke="currentColor"
                strokeWidth="4"
                fill="none"
              />
              <path
                className="opacity-75"
                fill="currentColor"
                d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"
              />
            </svg>
            Ajout...
          </span>
        ) : product.stock === 0 ? (
          'Rupture de stock'
        ) : (
          'Ajouter au panier'
        )}
      </button>

      {showToast && (
        <div className="fixed bottom-4 right-4 bg-green-500 text-white px-6 py-3 rounded-lg shadow-lg z-50 animate-slide-up">
          ✅ Produit ajouté au panier !
        </div>
      )}
    </>
  );
}
